function Table(p) {
   return (

      <table className="striped ">
         <thead>
            <tr>
               {p.h.map((item, index) => {
                  return <th key={index}>{item}</th>
               })}
            </tr>
         </thead>
         <tbody>
         {p.b.map((item, index) => {
                  return <td key={index}>{item}</td>
               })}
         </tbody>
      </table>
   )
}
